.myhand-panel {
  position: fixed;
  top: 320px;
  left: 20px;
  width: 580px;
  height: 230px;
  background-color: #c0c0c0;
  padding: 5px 5px 5px 5px;
  display: flex;
  align-items: flex-start;
  border-radius: 5px;
}

.myhand-card {
  position: absolute;
  top: 60px;
  width: 100px;
  height: 200px;
}

