.App {
  text-align: center;
  display: flex;
  align-items: flex-end;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.app-playerselecter {
  position: absolute;
  top: 20px;
  left: 50px;
  display: flex;
  width: 400px;
  justify-content: space-between;
}

.app-log {
  position: absolute;
  top: 150px;
  left: 50px;
  font-size: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
